import { API_KEY, ARTISTS_URL } from 'settings/api'
import fetch from 'node-fetch'

export const searchArtist = ( artistName ) => {

    return ( dispatch ) => {

        dispatch( {
            type: 'SEARCH_PENDING'
        } )

        return get( `${ARTISTS_URL}/${artistName}`, {
            params: {
                app_id: API_KEY,
            }
        } )

        .then( ( resp ) => {
            console.log( resp )
        } )

    }

}
